<template>
  <div v-show="ready">
  <div class="top-header"><input v-model="tmppage" class="page-input"/><span class="go-btn" @click="handlegopage">Go</span></div>
  <div class="hello">
    <ul class="videolist-wrap">

      <li v-for="v in videos" :key="v.id" @click="handleitemclick(v)"><img :src="v.iaddr" :hls="v.vaddr"/><span>{{v.id}}</span></li>

    </ul>
    <div >
      <span class="navbtn" @click="goprev">Prev</span><span class="pagenum">{{currentpage}}</span><span class="navbtn" @click="gonext">Next</span>
    </div>
  </div>
  <div v-if="showdetail" class="detail-wrap">
    <h4 class="detailtitle">{{detailtitle}}</h4>
    <VideoPlayer :options="playoption"></VideoPlayer>
    <div class="closebtn" @click="closedetail">Close</div>
  </div>
</div>
</template>

<script>
import VideoPlayer from '@/components/videoplayer.vue';
import 'video.js/dist/video-js.css';
const axios = require('axios');
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  components: {
		VideoPlayer
	},
  created(){
    window.logwrap.style="display:none;";
    var that=this;
    axios.get('https://www.crystalace.net/a.php')
  .then(function (response) {
    // handle success
    that.totalvideos=response.data[1]+1;
    that.ready = true;
  })
  .catch(function () {

  })
  .then(function () {
    // always executed
  });
  },
  data(){
    return {
      detailtitle:"",
      ready:false,
      tmppage:0,
      showdetail:false,
      totalvideos:0,
      player:null,
      currentpage:1,
      playoption:{},
      imgbaseurl:"https://hp.cloudfront.xyz/tmb/",
      videobaseurl:"https://hp.cloudfront.xyz/hls/mp4/"
    }
  },
  computed: {
    videos(){
      var tmpvideos=[];

      for(var i=1;i<=20;i++){
        var tmpid=this.totalvideos-(this.currentpage-1)*20-i;
        var tmpvideo={
          id:tmpid,
          iaddr:this.imgbaseurl+tmpid+`/`+Math.floor((Math.random() * 10) + 1)+`.jpg`,
          vaddr:this.videobaseurl+tmpid+`.mp4/index.m3u8`
        };
        tmpvideos.push(tmpvideo);
      }
      return tmpvideos;
    }
  },
  methods:{
    goprev(){
      this.currentpage=this.currentpage-1;
    },
    gonext(){
      this.currentpage=this.currentpage+1;
    },
    handlegopage(){
      this.currentpage=parseInt(this.tmppage);
    },
    handleitemclick(v){
      this.playoption = {
       autoplay:false,
       controls:true,
        width:document.body.clientWidth+"px",
        height:"300px",
        poster:this.imgbaseurl+v.id+`/`+'preview.jpg',
        sources:[{
          src:v.vaddr,
          type: "application/x-mpegURL"}]};
         //this.detailtitle=v.id;
      this.showdetail=true;
    },
    closedetail(){
      this.showdetail=false;
    }
  }
}
</script>

